$(".main-block-step-bottom-answer").click(function () {
  toggleAnswer();
});

var activeItem = $(".main-block:first-child");
activeItem.addClass("active");

activeItem.find(".main-block-step-body").slideDown(500);

function toggleAnswer() {
  var currentItem = activeItem;
  var newCurrentItem = currentItem.next(".main-block");
  currentItem.removeClass("active").addClass("passed");
  newCurrentItem.addClass("active");
  currentItem.find(".main-block-step-body").slideUp(500);
  newCurrentItem.find(".main-block-step-body").slideDown(500);
  activeItem = newCurrentItem;
}
